import React from 'react';
import { HashRouter, Switch , Route } from 'react-router-dom';
//import Login from './pages/login';
import Index from './pages/index';
//import Register from './pages/register';
//import DeleteUser from './pages/delete';
import Panel from './pages/panel';
import Poc from './pages/poc';
import Edit from './pages/edit';
import Authenticate from './pages/poc/authenticate';
//import ViewFig from './pages/viewFig';
import PainelDeControle from './pages/painelDeControle';

function Routes(){
    return (
        <HashRouter>
            <Switch>
                <Route path="/" exact component={Index} />
                <Route path="/panel" component={Panel} exact />
                {/*<Route path="/viewFig" component={ViewFig} />*/}
                <Route path="/poc" component={Authenticate} exact />
                <Route path="/poc/:id" component={Poc} />
                <Route path="/panel/:id" component={Edit} />
                <Route path="/controlPanel" component={PainelDeControle} />
            </Switch>
        </HashRouter>
    );
}

export default Routes;
