import React from 'react';

import api from '../../services/api';

export default function Index(props){

    async function handleSubmit(event){
        event.preventDefault();
        buttonActions[event.target.value]();
    }

    const variables = {
        _id: props.match.params.id,
        row: 0,
    };

    const buttonActions = {
        Novo(nameValue = '', barcodeValue = '', textsValue = ''){
            const row = variables.row;
            variables.row++;
            let contador = 0;
            const hr = (className) => {
                const hr = document.createElement('hr');
                hr.style = `
                    border-top: 1px solid black;
                `;
                hr.className = className;
                return hr;
            }
            hr.style = "border-top: 1px solid black";
            const columns = [];
            columns[0] = document.getElementById('SKU');
            columns[1] = document.getElementById('barcode');
            columns[2] = document.getElementById('texts');
            const buttonsCol = document.getElementById('buttons');

            const deleteButton = document.createElement('button');
            deleteButton.appendChild(document.createTextNode('X'));
            deleteButton.className = `${row}`;
            deleteButton.onclick = (event) => {
                event.preventDefault();
                deleteAll(`${row}`);
            }

            const newInput = () => {
                const newInput = document.createElement('input');
                switch(contador){
                    case 0:
                        contador++;
                        newInput.name = 'skus[]';
                        newInput.value = nameValue;
                        newInput.className = `${row}`;
                        break;
                    case 1:
                        contador++;
                        newInput.name = 'barcodes[]';
                        newInput.value = barcodeValue;
                        newInput.className = `${row}`;
                        break;
                    case 2:
                        contador = 0;
                        newInput.name = 'texts[]';
                        newInput.value = textsValue;
                        newInput.value = newInput.value.replace(',', ' ');
                        newInput.className = `${row}`;
                        break;
                    default:
                        break;
                }
                return newInput;
            }
            columns.forEach(element => {
                element.appendChild(newInput());
                element.appendChild(hr(row));
            })
            buttonsCol.appendChild(deleteButton);
            buttonsCol.appendChild(hr(row));
        },
        async Salvar(){
            let skusForms;
            try{
                skusForms = [...document.forms.formulario.elements['skus[]']]
            }
            catch{
                skusForms = document.forms.formulario.elements['skus[]'] ?
                [document.forms.formulario.elements['skus[]']] : [];
            }


            let barcodesForms;
            try{
                barcodesForms = [...document.forms.formulario.elements['barcodes[]']]
            }
            catch{
                barcodesForms = document.forms.formulario.elements['barcodes[]'] ?
                [document.forms.formulario.elements['barcodes[]']] : [];
            }

            let textsForms;
            try{
                textsForms = [...document.forms.formulario.elements['texts[]']]
            }
            catch{
                textsForms = document.forms.formulario.elements['texts[]'] ?
                [document.forms.formulario.elements['texts[]']] : [];
            }

            const skus = [];

            skusForms.forEach(element => {
                skus.push(element.value);
            })

            const barcodes = [];

            barcodesForms.forEach(element => {
                if(element.value !== '')
                barcodes.push(element.value);
            })

            const texts = [];

            textsForms.forEach(element => {
                if(element.value !== '') texts.push(element.value.split(' ').filter(element => !!element));
                else texts.push([]);
            })

            const id = variables._id;

            const dataToUpsert = {
                names: skus,
                texts,
                barcodes,
                classId: id,
            }

            await api.post('/updateSkus', dataToUpsert);
            
            window.location.reload();
        }
    }

    async function deleteAll(id){
        if(!window.confirm(`Tem certeza que deseja excluir a sku ? ` +
        `Esta ação não pode ser desfeita.`)) return;
        const login = window.prompt('Login');
        const password = window.prompt('Senha');
        if(login !== 'adm' || password !== 'adm') return;
        const divsToRemove = document.getElementsByClassName(`${id}`);
        for (var i = divsToRemove.length-1; i >= 0; i--) {
            if(divsToRemove[i].name === 'barcodes[]')
                api.post('/deleteSku', {barcode: divsToRemove[i].value})
            divsToRemove[i].remove();
        }
    }

    async function readDB(){
        const Class = await api.post('/getClassById', { id: variables._id });
        const Skus = (await api.post('/getAllSkus', {classId: variables._id})).data;
        Skus.forEach(element => {
            buttonActions.Novo(element.name, element.barcode, element.texts);
        });
        console.log(Skus)
        return Class;
    }
    readDB().then(({data}) =>{
        const formulario = document.getElementById('formulario');
        formulario.addEventListener('keypress', event =>{
            if(event.key === 'Enter'){
                event.preventDefault();
                buttonActions.Salvar();
            };
        });
        const skus = document.getElementById('skus');
        const head = document.getElementById('head');
        const hr = document.createElement('hr');
        head.appendChild(document.createTextNode(data.name));
        head.appendChild(hr);
        const newButton = document.createElement('input');
        newButton.type = 'submit';
        newButton.onclick = handleSubmit;
        newButton.value = 'Novo';
        newButton.style = 'background-color: #cccccc';
        skus.appendChild(newButton);
        const saveButton = document.createElement('input');
        saveButton.type = 'submit';
        saveButton.onclick = handleSubmit;
        saveButton.value = 'Salvar';
        saveButton.style = 'background-color: #ff4545';
        skus.appendChild(saveButton);
    });

    return (
        <form onSubmit={handleSubmit} name='formulario' id='formulario'>
            <div id="skus">
                <p onClick={() => props.history.goBack()}>Voltar</p>
                <div className="row" style={{
                    flexDirection: 'row',
                }}>
                    <div id="head" className="head" />
                    <div id="SKU" className="col-sm-4 col-xs-4">
                        SKU
                    </div>
                    <div id="barcode" className="col-sm-3 col-xs-3">
                        barcode
                    </div>
                    <div id="texts" className="col-sm-4 col-xs-3">
                        Textos
                    </div>
                    <div id="buttons" className="col-sm-1 col-xs-2">
                        <br />
                    </div>
                </div>
            </div>
        </form>
    );
}