import React from 'react';

export default function Index({ history }){
    const redirect = (target) => {
        history.push(`/${target}`)
    }
    return (
        <>
            <p onClick={() => redirect('panel')} className='link'>
                Painel de registros
            </p>
            <p onClick={() => redirect('poc')} className='link'>
                Visualizar
            </p>
            <p onClick={() => redirect('controlPanel')} className='link'>
                Painel de Controle
            </p>
        </>
    );
}