import React from 'react';
import api from '../../services/api';

export default function Index(props){

    async function handleSubmit(event){
        event.preventDefault();
        try{
            buttonActions[event.target.value]();
        }
        catch{

        }
    }

    async function deleteClass(_id, className){
        if(!window.confirm(`Tem certeza que deseja excluir a classe "${className}"? ` +
        `Esta ação não pode ser desfeita.`)) return;
        const login = window.prompt('Login');
        const password = window.prompt('Senha');
        if(login !== 'adm' || password !== 'adm') return;
        await api.post('/deleteClass', {_id});
        await api.post('/deleteSkuByClassId', {classId: _id})
        window.location.reload();
    }

    const buttonActions = {
        Novo(){
            const br = document.createElement('br')
            const hr = document.createElement('hr')
            hr.style = "border-top: 1px solid black";
            const classes = document.getElementById('newInputsArea');
            const newInput = document.createElement('input');
            newInput.name = 'newClass[]';
            newInput.value = '';
            const button = document.createElement('button');
            button.appendChild(document.createTextNode('...'));
            classes.appendChild(newInput);
            classes.appendChild(hr);
            classes.appendChild(br);
        },
        async Salvar(){
            try{
                let newClasses;
                try{
                    newClasses = [...document.forms.classes.elements['newClass[]']];
                }
                catch{
                    newClasses = document.forms.classes.elements['newClass[]'] ?
                    [document.forms.classes.elements['newClass[]']] : [];
                }

                let oldClasses;

                try{
                    oldClasses = [...document.forms.classes.elements['class[]']];
                }
                catch{
                    oldClasses = document.forms.classes.elements['class[]'] ?
                    [document.forms.classes.elements['class[]']] : [];
                }
                const Classes = [];
                await newClasses.forEach(element => {
                    if (element.value !== '') Classes.push({
                        name: element.value
                    })
                });
                await oldClasses.forEach(element => {
                    if (element.value !== '') Classes.push({
                        name: element.value, _id: element.id
                    })
                });
                await api.post('/updateClasses', {Classes});
            }
            catch(exception){
                alert(exception)
            }
            window.location.reload();
        },
    }



    async function readData(){
        const response = await api.post('/getAllClasses');
        return response
    }

    async function redirectToId(id){
        props.history.push(`/panel/${id}`);
    }


    const load = () => readData().then(({data}) => {
        data = data.Classes;
        const classes = document.getElementById('classes');
        classes.addEventListener('keypress', event =>{
            if(event.key === 'Enter'){
                event.preventDefault();
                buttonActions.Salvar();
            };
        });
        data.forEach(element => {
            const br = document.createElement('br')
            const hr = document.createElement('hr')
            hr.style = "border-top: 1px solid black";
            const newInput = document.createElement('input');
            newInput.name = 'class[]';
            newInput.value = element.name;
            const button = document.createElement('button');
            button.onclick = () => redirectToId(element._id)
            newInput.id = element._id;
            button.appendChild(document.createTextNode('...'));
            button.style='background-color:#dddddd; margin-left:10px;';
            const deleteButton = document.createElement('button');
            deleteButton.appendChild(document.createTextNode('X'));
            deleteButton.onclick = () => deleteClass(element._id, element.name);
            deleteButton.style='background-color:#cc3333; margin-left:10px;';
            classes.appendChild(newInput);
            classes.appendChild(button);
            classes.appendChild(deleteButton);
            classes.appendChild(hr);
            classes.appendChild(br);
        });
        const newInputsArea = document.createElement('div')
        newInputsArea.id = 'newInputsArea';
        classes.appendChild(newInputsArea);
        const newButton = document.createElement('input');
        newButton.type = 'submit';
        newButton.onclick = handleSubmit;
        newButton.value = 'Novo';
        newButton.style = 'background-color: #cccccc';
        classes.appendChild(newButton);
        const saveButton = document.createElement('input');
        saveButton.type = 'submit';
        saveButton.onclick = handleSubmit;
        saveButton.value = 'Salvar';
        saveButton.style = 'background-color: #ff4545';
        classes.appendChild(saveButton);
    })
    .catch(exception => {
        alert(exception);
    });

    load();

    return (
        <form onSubmit={handleSubmit} name="classes">
            <div className="row">
                <div id="classes" className="col-sm-12 col-xs-12">
                    <p>Classe</p>
                </div>
            </div>
        </form>
    );
}