import React from 'react';
import api from '../../services/api';

export default function Poc({ history, match }){
    const id = match.params.id;

    async function BeforeItems(){
        const before = (await api.post(`/info`, {
            path: 'before',
            image: `${id}.jpeg`,
        })).data;
        const beforeData = (await api.post(`/info`, {
            path: 'before',
            image: `${id}.json`,
        })).data;
        const list = document.createElement('ul');
        console.log(beforeData)
        if(!beforeData) return (false);
        beforeData.forEach(element => {
            const item = document.createElement('li');
            const Class = element.class.replace(/_/g, ' ');
            const options = document.createElement('select');
            element.skus.forEach(sku => {
                const option = document.createElement('option');
                option.appendChild(document.createTextNode(sku.sku));
                options.appendChild(option)
            });
            item.appendChild(document.createTextNode(Class));
            // item.appendChild(document.createElement('br'));
            // item.appendChild(options);
            // item.appendChild(document.createElement('br'));
            list.appendChild(item);
        });

        document.getElementById("beforeImage").src = `data:image/jpeg;base64,${before}`;
        document.getElementById("beforeItems").appendChild(list);

        return beforeData;
    }

    async function NowItems(){
        const now = (await api.post(`/info`, {
            path: 'now',
            image: `${id}.jpeg`,
        })).data;
        const nowData = (await api.post(`/info`, {
            path: 'now',
            image: `${id}.json`,
        })).data;
        const list = document.createElement('ul');
        const response = [];
        if(!nowData) return (false);
        nowData.forEach(element => {
            const item = document.createElement('li');
            const Class = element.class.replace(/_/g, ' ');
            const options = document.createElement('select');
            element.skus.forEach(sku => {
                const option = document.createElement('option');
                option.appendChild(document.createTextNode(sku.sku));
                options.appendChild(option);
            });
            response.push(Class);
            item.appendChild(document.createTextNode(Class));
            // item.appendChild(document.createElement('br'));
            // item.appendChild(options);
            // item.appendChild(document.createElement('br'));
            list.appendChild(item);
        });
        document.getElementById("nowImage").src = `data:image/jpeg;base64,${now}`;
        document.getElementById("nowItems").appendChild(list);

        return nowData;
    }

    function diff(nowItems, beforeItems){
        const response = [];
        console.log(nowItems);
        beforeItems.forEach(element => {
            let find = false;
            nowItems.forEach(item => {
                if (item.class === element.class){
                    find = true;
                }
            })
            if(!find){
                response.push(element);
            }
        })
        return response;
    }

    function renderMissingItems(missingItems){
        const items = document.createElement('ul');
        missingItems.forEach(element => {
            const item = document.createElement('li');
            item.appendChild(document.createTextNode(element.class));
            items.appendChild(item);
            const options = document.createElement('select');
            element.skus.forEach(sku => {
                const option = document.createElement('option');
                option.appendChild(document.createTextNode(sku.sku));
                options.appendChild(option);
            });
            items.appendChild(options);
        });
        document.getElementById('missingItems').appendChild(items);
    }

    function renderAddedItems(addedItems){
        const items = document.createElement('ul');
        addedItems.forEach(element => {
            const item = document.createElement('li');
            item.appendChild(document.createTextNode(element.class));
            items.appendChild(item);
            const options = document.createElement('select');
            element.skus.forEach(sku => {
                const option = document.createElement('option');
                option.appendChild(document.createTextNode(sku.sku));
                options.appendChild(option);
            });
            items.appendChild(options);
        });
        document.getElementById('addedItems').appendChild(items);
    }

    const load = async () => {
        const nowItems = await NowItems();
        const beforeItems = await BeforeItems();
        const missingItems = diff(nowItems, beforeItems);
        const addedItems = diff(beforeItems, nowItems);
        renderMissingItems(missingItems);
        renderAddedItems(addedItems);
    };

    load();

    return (
        <>
        <div className="row">
            <div className="col-sm-6">
                <h1>Imagem anterior</h1>
                <img
                    width='100%'
                    height='100%'
                    alt='não foi possível encontrar a imagem'
                    id='beforeImage'
                />
                <h2>
                    Itens encontrados:
                </h2>
                <nav id="beforeItems" />
            </div>
            <div className="col-sm-6">
                <h1>Ultima Imagem</h1>
                <img
                    width='100%'
                    height='100%'
                    alt='não foi possível encontrar a imagem'
                    id='nowImage'
                />
                <h2>
                    Itens encontrados:
                </h2>
                <nav id="nowItems" />
            </div>
        </div>
        <div className="row">
            <div className="col-sm-6">
                <h2>
                    Items removidos:
                </h2>
            <nav id="missingItems" />
            </div>
            <div className="col-sm-6">
                <h2>
                    Items adicionados:
                </h2>
                <nav id="addedItems" />
            </div>
        </div>
        </>
    );
}