import React, { Component } from 'react';
import api from '../../services/api';

export default class PainelDeControle extends Component{
    render(){
        async function readDB(){
            return (await api.post('/getConfig', {})).data;
        }

        async function handleSubmit(event){
            event.preventDefault();
            const newArn = document.getElementById('projectArnVersion').value;
            const awsConfig = (await api.post('/getConfig', {})).data;
            awsConfig.rekognitionPreferences.ProjectVersionArn = newArn;
            api.post('/setConfig', awsConfig);
            window.location.reload();
        }

        readDB().then(data => {
            const form = document.getElementById('pageForm');
            const projectVersionArn = data.rekognitionPreferences.ProjectVersionArn;
            document.getElementById('projectArnVersion').value = projectVersionArn;
            if(data.modelStatus.starting){
                form.appendChild(document.createElement('br'));
                form.appendChild(document.createTextNode(`Status do modelo: ${data.modelStatus.message}`));
                return;
            }
            if(data.modelStatus.ready){
                document.getElementById('stopButton').style.display = 'block';
            }
            else{
                document.getElementById('startButton').style.display = 'block';
            }
            form.appendChild(document.createElement('br'));
            form.appendChild(document.createTextNode(`${data.modelStatus.message}`));
        })

        async function requestToStartModel(){
            api.post('/startModel');
        };

        async function requestToStopModel(){
            api.post('/stopModel');
        };

        function startModel(event){
            event.preventDefault();
            requestToStartModel();
            window.location.reload();
        }

        function stopModel(event){
            event.preventDefault();
            requestToStopModel();
            window.location.reload();
        }

        return (
        <>
            <p>
                Painel de Controle
            </p>
            <form onSubmit={handleSubmit}>
                <center id="pageForm">
                    <input type="text" name="projectArnVersion" id="projectArnVersion" />
                    <input type="submit" value="Atualizar Project Version Arn" />
                    <br />
                    <button id='startButton' style={{display:'none'}} onClick={startModel}> Iniciar Modelo </button>
                    <button id='stopButton' style={{display:'none'}} onClick={stopModel}> Parar Modelo </button>
                </center>
            </form>
        </>
        )
    }
}